@import url('https://fonts.googleapis.com/css?family=Libre+Franklin:400,400i,600,600i');
@import url('https://fonts.googleapis.com/css?family=Roboto|Varela+Round');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font: 400 16px/1.6 'Libre Franklin', sans-serif;
    color: rgb(0,0,0);
    overflow-x: hidden;
    word-wrap: break-word;
}

h1, h2, h3 {
    font-weight: 600;
}

h1 {
    margin-top: 1rem;
    font-size: 2rem;
    line-height: 1em;
    text-align: center;
    margin-bottom: 2rem;
}

h2 {
    font-size: 1.5rem;
    line-height: 1em;
    margin-bottom: 1rem;
}

h3 {
    font-size: 1.25rem;
    margin-bottom: 1rem;
}

abbr {
    text-decoration: none;
}

.topHeader {
    background: linear-gradient(to bottom, black, rgb(63,81,181));
}

.topHeader > div {
    cursor: pointer;
}

header ul {
    list-style-type: none;
    padding: 0;
}
header ul li {
    position:relative
}

.listMenu {
    position: absolute;
    top: 44px;
    left: 8px;    
    background-color: #e0e0e0;
}
.listMenu md-list-item{
    padding: 0;
}

.listMenu md-list-item::before{
    display: none;
}

header h2 {
    margin: 0;
}

main md-content {
    padding: 10px;
}
.container{
    max-width: 1200px;
}

p {
    font-size: 1rem;
    margin-bottom: 1rem;
}

.footer {
    background: linear-gradient(to bottom, rgb(63,81,181), black);
    font-size: 0.7rem;
}
